.cash-sure-wrap {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 120vh;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    background: rgba(233, 228, 228, 0.5);
}

.cash-sure-wrap header {
    width: 100%;
    text-align: center;
    background-image: url('../../images/login-bg.png');
    background-size: cover;
    color: #fff;
    font-size: 1.5em;
    padding: 2% 0;
}

.cash-sure-wrap img {
    width: 15%;
    margin: 10% 0 2%;
}

.cash-sure-wrap div {
    width: 74%;
    display: -ms-flexbox;
    display: flex;
    padding: 2% 4%;
    -ms-flex-direction: column;
        flex-direction: column;
    border-radius: 4px;
    background-color: #fff;
    margin-top: 4%;
}

.cash-sure-wrap div span {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    line-height: 2;
}
.cash-sure-wrap a{
    width: 74%;
    background-image: url('../../images/login-bg.png');
    padding: 3% 0;
    text-align: center;
    color: #fff;
    font-size: 1.5em;
    margin-top: 5%;
    border-radius: 10px;
}


