.system-wrap {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.system-wrap header {
  padding: 3% 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-image: url('../../images/login-bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

.system-wrap header i {
  width: 30px;
  height: 30px;
  margin-left: 6px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url('../../images/back.png');
}

.system-wrap header span {
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-align: center;
  margin-right: 6px;
  font-size: 1.5em;
}

.system-wrap>div{
  height: 93vh;
  background: rgba(235,235,235,0.8);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0 4%;
}
.system-wrap>div>span{
  width: 30%;
  background: rgba(0,0,0,0.3);
  padding: 3%;
  border-radius: 5px;
  margin: 5% 0;
}
.system-wrap>div>div{
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  padding: 2%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 30vh;
}




